/* stylelint-disable */
/* stylelint-enable */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswald-400.woff2") format("woff2"), url("../fonts/oswald-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Oswald";
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswald-500.woff2") format("woff2"), url("../fonts/oswald-500.woff") format("woff");
}
:root {
  --black: #000000;
  --black-2: #111111;
  --black-3: #222222;
  --grey: #444444;
  --grey-2: #666666;
  --grey-3: #cdcdcd;
  --grey-4: #dcdcdc;
  --grey-5: #f2f2f2;
  --white: #ffffff;
  --white-2: #e6e6e6;
  --green: #68b738;
  --green-2: #5eaa2f;
  --green-3: #65a93c;
  --orange: #ff8282;
}

body {
  min-width: 320px;
  margin: 0 auto;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: var(--grey);
  background-color: var(--white);
}

*,
::before,
::after {
  box-sizing: border-box;
}

img {
  display: block;
}

.show-main-nav {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.page__title {
  font-weight: 500;
  font-size: 36px;
  line-height: 36px;
  color: var(--black);
}

.page__sub-title {
  font-size: 24px;
  line-height: 24px;
  color: var(--black);
}

.header {
  display: flex;
  justify-content: space-between;
  min-height: 65px;
  padding: 10px 0 0 0;
  box-sizing: border-box;
  min-width: 320px;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .header {
    min-width: 768px;
    padding-right: 30px;
    padding-left: 30px;
  }
}
@media (min-width: 1440px) {
  .header {
    width: 1440px;
    padding-right: 110px;
    padding-left: 110px;
  }
}

.header__hamburger {
  box-sizing: content-box;
  width: 24px;
  height: 18px;
  margin: 4px -8px 0 0;
  padding: 8px;
  background-color: unset;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__hamburger {
    display: none;
  }
}

.header__hamburger-inner {
  position: relative;
  height: 2px;
  background-color: var(--black);
  transition: all 0.3s ease;
}

.header__hamburger-inner--open {
  transform: rotate(135deg);
}

.header__hamburger-inner::before,
.header__hamburger-inner::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: inherit;
  background-color: inherit;
  transition: inherit;
}

.header__hamburger-inner::before {
  top: -8px;
}

.header__hamburger-inner::after {
  top: 8px;
}

.header__hamburger-inner--open::before,
.header__hamburger-inner--open::after {
  top: auto;
  transform: rotate(90deg);
}

.header__hamburger-inner--open::after {
  transform: rotate(180deg);
}

.footer {
  background-color: var(--grey-5);
}

.logo:hover {
  opacity: 0.8;
}

.logo:active {
  opacity: 0.6;
}

.site-navigation {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-navigation--open {
  position: fixed;
  top: 65px;
  right: 0;
  left: 0;
  z-index: 1;
  display: block;
  text-align: center;
  background-color: var(--white);
}

.site-navigation__item {
  padding-top: 23px;
  padding-bottom: 20px;
  border-top: 1px solid var(--white-2);
}

.site-navigation__link {
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--black);
  text-transform: uppercase;
  text-decoration: none;
}

.intro {
  position: relative;
  color: var(--white);
}

.intro::before {
  content: "";
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 302px;
  background-color: var(--green-3);
  background-image: url("../img/interface/background/index-background-mobile@1x.png");
  background-repeat: no-repeat;
  background-position-x: center;
}

.intro__inner {
  display: flex;
  flex-direction: column;
}

.intro__title {
  font-size: 36px;
  line-height: 36px;
}

.intro__text {
  line-height: 14px;
  text-transform: uppercase;
}

.button {
  padding: 7px 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: var(--white);
  text-transform: uppercase;
  text-decoration: none;
  background-color: var(--green);
  border: none;
  cursor: pointer;
}

.button--transparent {
  color: var(--grey);
  background-color: transparent;
}

.popular-category__item {
  background-color: var(--grey-5);
}

.popular-category__title {
  font-weight: 500;
  font-size: 24px;
  line-height: 37px;
  color: var(--black);
  text-transform: uppercase;
}

.popular-category__button {
  position: relative;
  padding: 0 45px 0 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  color: var(--black);
  text-transform: uppercase;
  text-decoration: none;
}

.popular-category__button:active {
  opacity: 0.3;
}

.popular-category__button::before {
  content: "";
  position: absolute;
  top: 26%;
  right: 8px;
  display: block;
  width: 24px;
  height: 12px;
  background-image: url("../img/interface/other/arrow-right@wider.svg");
  background-position: right;
}

.popular-category__button:hover::before {
  right: 0;
  width: 32px;
  transition: all 0.4s ease;
}

.benefits__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.case {
  background: linear-gradient(0deg, var(--grey-5) 100%, var(--white) 100%);
}

.case__progress-definition {
  font-size: 12px;
  line-height: 12px;
}

.case__progress-value {
  margin: 0;
  font-size: 24px;
  line-height: 24px;
  color: var(--black);
  text-transform: uppercase;
}

.case__price {
  font-weight: 500;
  line-height: 20px;
  color: var(--black);
  text-transform: uppercase;
}

.slider {
  font-size: 16px;
  line-height: 20px;
  color: var(--black);
  text-transform: uppercase;
}

.address--background {
  background-color: var(--grey-5);
}

.address__title {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: var(--black-2);
  text-transform: uppercase;
}

.address__text {
  font-style: unset;
  line-height: 20px;
}

.product-item {
  font-size: 12px;
  line-height: 16px;
  color: var(--black-3);
}

.product-item--showe-more {
  text-decoration: none;
}

.product-item__title {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.product-item__image {
  object-fit: contain;
  object-position: bottom;
}

.product-item__button {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}

.additional-item {
  line-height: 14px;
}

.additional-item__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--black-3);
  text-transform: uppercase;
}

.additional-item__button {
  font-size: 16px;
  line-height: 20px;
}

.banner {
  display: flex;
  min-height: 290px;
  padding: 0 60px 43px;
  text-align: center;
  background-color: var(--green-3);
  background-image: url("../img/interface/background/gift-mobile@1x.png");
  background-repeat: no-repeat;
  background-position-x: center;
}

.banner__text {
  margin: auto 0 0;
  font-size: 16px;
  line-height: 20px;
  color: var(--white);
}

.form {
  color: var(--grey);
  text-transform: uppercase;
}

.form__lead {
  color: var(--black);
}

.form__fieldset {
  border: none;
}
.form__fieldset--target {
  color: var(--black-3);
}

.form__label {
  font-size: 16px;
  line-height: 18px;
}

.form__input {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: inherit;
  text-transform: inherit;
}

/* stylelint-disable */
.form__input::-webkit-outer-spin-button,
.form__input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.form__input[type=number] {
  -moz-appearance: textfield;
}

/* stylelint-enable */